import { Component } from 'react'
import { connect } from 'react-redux'

// import {menuService} from '../'
//icons:
import SearchIcon from '@material-ui/icons/Search';


//functions:
import { setBackground } from '../../../../../store/actions/boardAction.js'
import { setFilter } from '../../../../../store/actions/menuAction.js'

//imgs:
import src1 from '../../../../../assets/bgImgs/image-01.jpg'
import src2 from '../../../../../assets/bgImgs/image-02.jpg'
import src3 from '../../../../../assets/bgImgs/image-03.jpg'
import src4 from '../../../../../assets/bgImgs/image-04.jpg'
import src5 from '../../../../../assets/bgImgs/image-05.jpg'
import src6 from '../../../../../assets/bgImgs/image-06.jpg'
import src7 from '../../../../../assets/bgImgs/image-07.jpg'
import src8 from '../../../../../assets/bgImgs/image-08.jpg'
import src9 from '../../../../../assets/bgImgs/image-09.jpg'
import src10 from '../../../../../assets/bgImgs/image-10.jpg'
import src11 from '../../../../../assets/bgImgs/image-11.jpg'
import src12 from '../../../../../assets/bgImgs/image-12.jpg'
import src13 from '../../../../../assets/bgImgs/image-13.jpg'
import src14 from '../../../../../assets/bgImgs/image-14.jpg'
import src15 from '../../../../../assets/bgImgs/image-15.jpg'
import src16 from '../../../../../assets/bgImgs/image-16.jpg'
import src17 from '../../../../../assets/bgImgs/image-17.jpg'
import src18 from '../../../../../assets/bgImgs/image-18.jpg'
import src19 from '../../../../../assets/bgImgs/image-19.png'

class _PhotosArea extends Component {

    state = {
        photoSearch: '',
        filter: '',
    }

    bgImgs = [
        { src: src19, _id: 'img119', title: "beach" },
        { src: src1, _id: 'img101', title: "sunset" },
        { src: src2, _id: 'img102', title: "Snowy mountains" },
        { src: src3, _id: 'img103', title: "Mountains at sunset" },
        { src: src4, _id: 'img104', title: "clouds" },
        { src: src5, _id: 'img105', title: "Cliff on a stream" },
        { src: src6, _id: 'img106', title: "Stars" },
        { src: src7, _id: 'img107', title: "Bright background" },
        { src: src8, _id: 'img108', title: "Wall painting" },
        { src: src9, _id: 'img109', title: "Snowy village" },
        { src: src10, _id: 'img110', title: "stream" },
        { src: src11, _id: 'img111', title: "Desert" },
        { src: src12, _id: 'img112', title: "A flowering tree" },
        { src: src13, _id: 'img113', title: "Cherries" },
        { src: src14, _id: 'img114', title: "Tree leaves" },
        { src: src15, _id: 'img115', title: "Orange tree" },
        { src: src16, _id: 'img116', title: "Tangled stream" },
        { src: src17, _id: 'img117', title: "Cloudy mountains" },
        { src: src18, _id: 'img118', title: "Rear passage" }
    ]

    handleChange = ({ target }) => {
        const field = target.name
        const value = target.value
        this.setState(prevState => ({ filterBy: { ...prevState.filterBy, [field]: value } }), () => {
            this.props.setFilter(this.state.filterBy)
        })
    }

    selectImg = (imgSrc) => {
         this.props.setBackground(this.props.board, { backgroundImage: `url(${imgSrc})` }) 
         this.props.toggleMenu()
        }

    get imgs() {
        const { filter } = this.state
        const regex = new RegExp(filter, 'i')
        return this.bgImgs.filter(img => regex.test(img.title))
    }

    render() {
        const imgs = this.imgs
        return (
            <section className="photo-type">
                <div className="flex search-photo">
                    <SearchIcon /><input type="text"
                        name="filter"
                        onChange={({ target }) => this.setState({ filter: target.value })}
                        placeholder="Photos"
                        autoComplete="off"
                        autoFocus
                    />
                </div>
                <div className="photos-area">
                    {this.imgs.map((img, idx) => {
                        return <div className="bgImg" key={img._id}
                            onClick={() => { this.selectImg(img.src) }}>
                            <img src={img.src} />
                        </div>
                    })}
                </div>
                <span className="go-back" onClick={this.props.goBack}>Go back to backgrounds</span>
            </section>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        board: state.boardModule.board,
        filterBy: state.menuModule.filterBy
    }
}
const mapDispatchToProps = {
    setBackground,
    setFilter
}
export const PhotosArea = connect(mapStateToProps, mapDispatchToProps)(_PhotosArea)

